.image-cropper {
    width: 100px;
    height: 100px;
    overflow: hidden;
    position: relative;
}

img {
    position: absolute;
    left: -100%;
    right: -100%;
    top: -100%;
    bottom: -100%;
    margin: auto;
    max-height: 150%;
    max-width: 150%;
}

.profileContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    margin-left: 10%;
}